import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { UserViewModel } from '../../Model/ViewModel/UserViewModel';
import { GlobalComponent } from '../../components/global/global';


/**
 * Generated class for the UserModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-add-edit-modal',
  templateUrl: 'user-add-edit-modal.html',
})
export class UserAddEditModalPage {
item:any;
mode:string;
CanEditAuth: boolean;
change_mode:string;
confirm_password:string;
  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,private global: GlobalComponent) {
    this.item=new UserViewModel();
    this.item.userName=this.navParams.data.item.userName
    this.item.Email=this.navParams.data.item.Email;
    this.mode=this.navParams.data.mode;
    this.change_mode=this.navParams.data.change_mode;
    this.CanEditAuth=this.navParams.data.CanEditAuth;
  }


  Save() {
    if(this.confirm_password!=this.item.password)
    {
      this.global.showPopup("密碼錯誤","確認密碼不一致");
      return;
    }
    if(this.mode==="POST")
    {
      this.item.roleName=[];
      this.item.roleName.push("users");
    }
    if(this.change_mode==="detail")
      this.item.password="";
    console.log(this.item);
      this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad UserModalPage');
  }

}



